"use client"

/**
 * WebSocket hook for real-time requests queue updates
 * Handles automatic reconnection with exponential backoff
 */

import { useEffect, useRef, useCallback } from "react"
import { authService } from "@/lib/auth-service"

export interface WSMessage {
  event: "request_created" | "request_updated" | "state_transition"
  request?: any
  request_id?: number
  from?: string
  to?: string
  changed_by?: string
  timestamp?: string
  note?: string
}

const INITIAL_BACKOFF = 1000
const MAX_BACKOFF = 30000

export function useRequestsWS(onEvent: (message: WSMessage) => void) {
  const wsRef = useRef<WebSocket | null>(null)
  const backoffRef = useRef(INITIAL_BACKOFF)
  const closedManuallyRef = useRef(false)
  const reconnectTimeoutRef = useRef<NodeJS.Timeout>()

  const getWsUrl = useCallback(() => {
    const token = authService.getAccessToken()
    if (!token) return null

    const protocol = typeof window !== "undefined" && window.location.protocol === "https:" ? "wss" : "ws"
    const host = typeof window !== "undefined" ? window.location.host : "localhost:3000"
    return `${protocol}://${host}/ws/requests/?token=${token}`
  }, [])

  const connect = useCallback(() => {
    const wsUrl = getWsUrl()
    if (!wsUrl) return

    try {
      const ws = new WebSocket(wsUrl)
      wsRef.current = ws

      ws.onopen = () => {
        console.log("[v0] WebSocket connected")
        backoffRef.current = INITIAL_BACKOFF
      }

      ws.onmessage = (event) => {
        try {
          const message = JSON.parse(event.data)
          onEvent(message)
        } catch (error) {
          console.error("[v0] Failed to parse WS message:", error)
        }
      }

      ws.onclose = () => {
        console.log("[v0] WebSocket closed")
        if (closedManuallyRef.current) return

        reconnectTimeoutRef.current = setTimeout(() => {
          console.log("[v0] Attempting to reconnect with backoff:", backoffRef.current)
          connect()
          backoffRef.current = Math.min(backoffRef.current * 2, MAX_BACKOFF)
        }, backoffRef.current)
      }

      ws.onerror = (error) => {
        console.error("[v0] WebSocket error:", error)
      }
    } catch (error) {
      console.error("[v0] Failed to create WebSocket:", error)
    }
  }, [getWsUrl, onEvent])

  useEffect(() => {
    closedManuallyRef.current = false
    connect()

    return () => {
      closedManuallyRef.current = true
      if (reconnectTimeoutRef.current) {
        clearTimeout(reconnectTimeoutRef.current)
      }
      if (wsRef.current) {
        wsRef.current.close()
      }
    }
  }, [connect])

  return {
    isConnected: wsRef.current?.readyState === WebSocket.OPEN,
    close: () => {
      closedManuallyRef.current = true
      wsRef.current?.close()
    },
  }
}
