"use client"

import { useEffect, useState, useCallback } from "react"
import { useRouter } from "next/navigation"
import { authService } from "@/lib/auth-service"
import { requestsService, type RequestData, type ListRequestsParams } from "@/lib/requests-service"
import { useRequestsWS, type WSMessage } from "@/hooks/use-requests-ws"
import Image from "next/image"

export default function QueuePage() {
  const router = useRouter()
  const [requests, setRequests] = useState<RequestData[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState("")
  const [wsConnected, setWsConnected] = useState(false)

  // Filters
  const [categoryFilter, setCategoryFilter] = useState<string>("")
  const [statusFilter, setStatusFilter] = useState<string>("")
  const [fabTypeFilter, setFabTypeFilter] = useState<string>("")
  const [searchFilter, setSearchFilter] = useState("")

  // Check authentication and authorization
  useEffect(() => {
    const token = authService.getAccessToken()
    if (!token) {
      router.push("/login")
      return
    }

    // Verify user is POC or admin
    const user = authService.getCurrentUser()
    if (!user || (!user.is_poc && !user.is_admin)) {
      router.push("/dashboard")
    }
  }, [router])

  // Build filter params
  const getFilterParams = useCallback((): ListRequestsParams => {
    const params: ListRequestsParams = {}
    if (categoryFilter) params.category = categoryFilter as any
    if (statusFilter) params.status = statusFilter as any
    if (fabTypeFilter) params.fab_type = fabTypeFilter as any
    if (searchFilter) params.search = searchFilter
    return params
  }, [categoryFilter, statusFilter, fabTypeFilter, searchFilter])

  // Fetch initial queue snapshot
  const fetchQueue = useCallback(async () => {
    try {
      setLoading(true)
      setError("")
      const params = getFilterParams()
      const response = await requestsService.listRequests(params)
      setRequests(response.results)
    } catch (err) {
      console.error("[v0] Error fetching queue:", err)
      setError(err instanceof Error ? err.message : "Failed to fetch queue")
    } finally {
      setLoading(false)
    }
  }, [getFilterParams])

  // WebSocket handler
  const handleWsEvent = useCallback((message: WSMessage) => {
    if (message.event === "request_created" || message.event === "request_updated") {
      setRequests((prev) => {
        const filtered = prev.filter((r) => r.id !== message.request?.id)
        return [message.request, ...filtered].sort(
          (a, b) => new Date(b.created_at).getTime() - new Date(a.created_at).getTime(),
        )
      })
    } else if (message.event === "state_transition") {
      setRequests((prev) => prev.map((r) => (r.id === message.request_id ? { ...r, status: message.to as any } : r)))
    }
  }, [])

  // Connect to WebSocket
  const { isConnected } = useRequestsWS(handleWsEvent)

  useEffect(() => {
    setWsConnected(isConnected)
  }, [isConnected])

  // Fetch queue on mount and when filters change
  useEffect(() => {
    fetchQueue()
  }, [fetchQueue])

  const handleStatusChange = async (requestId: number, newStatus: string) => {
    try {
      await requestsService.changeRequestStatus(requestId, newStatus)
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to change status")
    }
  }

  const filteredRequests = requests.filter((req) => {
    if (categoryFilter && req.category !== categoryFilter) return false
    if (statusFilter && req.status !== statusFilter) return false
    if (fabTypeFilter && req.fabrication?.fab_type !== fabTypeFilter) return false
    return true
  })

  return (
    <div className="min-h-screen bg-white">
      {/* Header with Logos */}
      <header className="bg-white border-b border-gray-200 sticky top-0 z-40">
        <div className="max-w-7xl mx-auto px-4 py-4 flex items-center justify-between">
          <div className="flex items-center gap-4">
            <Image
              src="/images/image.png"
              alt="Smart India Hackathon"
              width={120}
              height={40}
              className="h-10 w-auto"
            />
            <Image src="/images/gitam-logo-20-282-29.png" alt="GITAM" width={60} height={40} className="h-10 w-auto" />
          </div>
          <h1 className="text-2xl font-bold" style={{ color: "#002449" }}>
            Virtual Queue
          </h1>
          <div className="text-sm text-gray-600">
            WS:{" "}
            <span style={{ color: wsConnected ? "#078e31" : "#f75700" }}>
              {wsConnected ? "Connected" : "Disconnected"}
            </span>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 py-8">
        {/* Filters */}
        <div className="bg-white rounded-lg border border-gray-200 p-6 mb-6">
          <h2 className="text-lg font-semibold mb-4" style={{ color: "#f75700" }}>
            Filters
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Category</label>
              <select
                value={categoryFilter}
                onChange={(e) => setCategoryFilter(e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
              >
                <option value="">All Categories</option>
                <option value="BOM">BOM</option>
                <option value="ADDITIONAL">Additional Materials</option>
                <option value="FABRICATION">Fabrication</option>
              </select>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Status</label>
              <select
                value={statusFilter}
                onChange={(e) => setStatusFilter(e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
              >
                <option value="">All Status</option>
                <option value="Submitted">Submitted</option>
                <option value="Processing">Processing</option>
                <option value="Issued">Issued</option>
                <option value="Cannot be Processed">Cannot be Processed</option>
              </select>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Fabrication Type</label>
              <select
                value={fabTypeFilter}
                onChange={(e) => setFabTypeFilter(e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
              >
                <option value="">All Types</option>
                <option value="3D">3D Printing</option>
                <option value="LASER">Laser Cutting</option>
                <option value="OTHER">Other</option>
              </select>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Search</label>
              <input
                type="text"
                placeholder="Search notes, items..."
                value={searchFilter}
                onChange={(e) => setSearchFilter(e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
            </div>
          </div>
        </div>

        {/* Error Message */}
        {error && (
          <div className="bg-red-50 border border-red-200 rounded-lg p-4 mb-6">
            <p className="text-red-800">{error}</p>
          </div>
        )}

        {/* Queue Grid */}
        {loading ? (
          <div className="text-center py-12">
            <p className="text-gray-600">Loading queue...</p>
          </div>
        ) : filteredRequests.length === 0 ? (
          <div className="text-center py-12">
            <p className="text-gray-600">No requests found</p>
          </div>
        ) : (
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-4">
            {/* Submitted Column */}
            <div>
              <h3
                className="text-lg font-semibold mb-4 pb-2 border-b-2"
                style={{ borderColor: "#f75700", color: "#002449" }}
              >
                Submitted ({filteredRequests.filter((r) => r.status === "Submitted").length})
              </h3>
              <div className="space-y-3">
                {filteredRequests
                  .filter((r) => r.status === "Submitted")
                  .map((req) => (
                    <RequestCard
                      key={req.id}
                      request={req}
                      onStatusChange={() => handleStatusChange(req.id, "Processing")}
                      actionLabel="Accept"
                    />
                  ))}
              </div>
            </div>

            {/* Processing Column */}
            <div>
              <h3
                className="text-lg font-semibold mb-4 pb-2 border-b-2"
                style={{ borderColor: "#078e31", color: "#002449" }}
              >
                Processing ({filteredRequests.filter((r) => r.status === "Processing").length})
              </h3>
              <div className="space-y-3">
                {filteredRequests
                  .filter((r) => r.status === "Processing")
                  .map((req) => (
                    <RequestCard
                      key={req.id}
                      request={req}
                      onStatusChange={() => handleStatusChange(req.id, "Issued")}
                      actionLabel="Mark Issued"
                    />
                  ))}
              </div>
            </div>

            {/* Issued/Completed Column */}
            <div>
              <h3
                className="text-lg font-semibold mb-4 pb-2 border-b-2"
                style={{ borderColor: "#007367", color: "#002449" }}
              >
                Completed (
                {filteredRequests.filter((r) => r.status === "Issued" || r.status === "Cannot be Processed").length})
              </h3>
              <div className="space-y-3">
                {filteredRequests
                  .filter((r) => r.status === "Issued" || r.status === "Cannot be Processed")
                  .map((req) => (
                    <RequestCard key={req.id} request={req} isCompleted />
                  ))}
              </div>
            </div>
          </div>
        )}
      </main>
    </div>
  )
}

function RequestCard({
  request,
  onStatusChange,
  actionLabel,
  isCompleted = false,
}: {
  request: RequestData
  onStatusChange?: () => void
  actionLabel?: string
  isCompleted?: boolean
}) {
  const getCategoryColor = (category: string) => {
    switch (category) {
      case "BOM":
        return "#002449"
      case "ADDITIONAL":
        return "#007367"
      case "FABRICATION":
        return "#f75700"
      default:
        return "#078e31"
    }
  }

  return (
    <div className="bg-white border border-gray-200 rounded-lg p-4 hover:shadow-lg transition-shadow">
      <div className="flex items-start justify-between mb-3">
        <span
          className="inline-block px-3 py-1 text-xs font-semibold text-white rounded-full"
          style={{ backgroundColor: getCategoryColor(request.category) }}
        >
          {request.category}
        </span>
        {request.position && <span className="text-xs font-semibold text-gray-500">#{request.position}</span>}
      </div>

      <h4 className="font-semibold text-gray-900 mb-2">{request.notes || "Untitled Request"}</h4>

      {request.fabrication && (
        <div className="mb-3 text-sm text-gray-600">
          <p>
            3D Type: <strong>{request.fabrication.fab_type}</strong> - {request.fabrication.name}
          </p>
        </div>
      )}

      {request.bom_items && request.bom_items.length > 0 && (
        <div className="mb-3 text-sm text-gray-600">
          <p>Items: {request.bom_items.length}</p>
        </div>
      )}

      {request.additional_items && request.additional_items.length > 0 && (
        <div className="mb-3 text-sm text-gray-600">
          <p>Items: {request.additional_items.length}</p>
        </div>
      )}

      <p className="text-xs text-gray-500 mb-3">{new Date(request.created_at).toLocaleDateString()}</p>

      {!isCompleted && onStatusChange && (
        <button
          onClick={onStatusChange}
          className="w-full px-3 py-2 text-sm font-medium text-white rounded-lg transition-colors"
          style={{ backgroundColor: "#078e31" }}
          onMouseEnter={(e) => (e.currentTarget.style.opacity = "0.9")}
          onMouseLeave={(e) => (e.currentTarget.style.opacity = "1")}
        >
          {actionLabel}
        </button>
      )}

      {isCompleted && (
        <div
          className="px-3 py-2 text-sm font-medium text-center rounded-lg"
          style={{ backgroundColor: "#f0e0c1", color: "#002449" }}
        >
          {request.status}
        </div>
      )}
    </div>
  )
}
